
import { Component, Emit, Prop, Vue, Watch } from 'vue-property-decorator';
import ActionBlock from '../../components/search/actionBlock.vue';
import { Form } from "element-ui";
import moment from 'moment';
import Service from '../../https/alarm/list'
@Component({
  components:{
    ActionBlock
  }
})
export default class InsDetail extends Vue{
  $message;
  @Prop(Boolean) visible!:Boolean;
  @Prop(String) id:String;
  @Prop(String) title:String

  private form_data:any={
      name:'',
      email:'',
      phone:'',

  }

  created() {
    if(this.id!==""){
      this.get_contact_detail()
    }
  }
  private validate_eamil:any = (rule:any, value:string, callback:any)=>{
        if(value.length>0 && value.indexOf('@')>0 && value.indexOf('@')<value.length-1){
            return callback()
        }else{
            return callback(new Error("请输入正确的邮箱"))
        }
  }
  private validate_phone:any = (rule:any, value:string, callback:any)=>{
      let reg = /^1[0-9]{10}$/
      if(reg.test(value)){
          return callback()
      }else{
          return callback(new Error("请输入正确的手机号码"))
      }
    }
  private async get_contact_detail(){
    let res:any = await Service.get_contact_detail({
        id:this.id,
      })
      if(res.code==='Success'){
        const {name,email,phone}=res.data
          this.form_data={
            name,
            email,
            phone
          }
      }else{
        
      }
  }
  private confirm(){
      const form = this.$refs.form as Form
      form.validate(async (valid:boolean)=>{
          if(valid){
            if(this.id===""){
              let res:any = await Service.add_contact({
                ...this.form_data
              })
              if(res.code==='Success'){
                this.$message.success("新建联系人任务下发成功")
                this.back('1')
              }else{
                this.back('0')
              }
            }else{
              let res:any = await Service.update_contact({
                ...this.form_data,
                id:this.id
              })
              if(res.code==='Success'){
                this.$message.success("编辑联系人任务下发成功")
                this.back('1')
              }else{
                this.back('0')
              }
            }
            
            
          }
      })
  }
  @Emit("close")
  private back(val){
    const form = this.$refs.form as Form
    form.resetFields()
  }
  
}
